.code-demo {
  color: white;
  background-color: #0d6efd;
  line-height: 2;
  vertical-align: middle;
  border-radius: 0.25rem;
  font-size: 1rem;
  height: 32px;
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
  border: 1px solid;
  text-decoration: none;
  height: 100%;
}

.code-demo:hover {
  color: black;
}

.project-links {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%, -50%);
}
